* {
    box-sizing: border-box;
    }


.buttonContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    }

.Button {
    animation: App-logo-spin infinite 3s linear;
    width: 20vw;
    height: 20vh;
border-radius: 8px;
    background-color: #424242;
    font-size: 20px;
    color: wheat;
    }


@keyframes App-logo-spin {
    0% {
        transform: scale(1);
        }
    50% {
        transform: scale(1.2);
        }
    100% {
        transform: scale(1);
        }
    }
