.photoContainer {
    display: flex;
    flex-wrap: wrap;
    }

.singlePhoto{
    flex-basis: 30%;
    height: 25vh;
    object-fit: cover;
    border: 1px solid wheat;
    background-color: black;
    }

.singlePhoto img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left;
    }
